<template>
	<main>
		<div class="top">
			<i class="iconfont icon-zuojiantou" @click="back"></i>
			<h1></h1>
		</div>
		<div class="con">
			  ......
		</div>	
	</main>
</template>

<script>
	export default{
		methods:{
			back(){
				this.$router.go(-1)
			}
		}
	}
</script>

<style scoped>
	main{
		background: #f2f2f2;
		height: 100vh;
		position: relative;
		z-index: 5;
	}
	.top {
		display: flex;
		padding: 13px 20px;
		height: 45px;
		background: #fff;
		font-size: 20px;
		box-sizing: border-box;
	}
	.top h1{
		flex-grow: 1;
		text-align: center;
	}
	.top *{
		align-self: center;
	}
	.top i{
		color:#eb6841;
		font-size: 25px;
	}
	.con{
		margin: 15px;
	    line-height: 29px;
	    color: #333;
	}	
</style>


